import * as z from "zod";

/**
 * Schema   Checkout
 */
export const checkoutFormSchema = z.object({
  firstName: z.string().min(1, "  "),
  lastName: z.string().optional(), //     
  phone: z
    .string()
    .min(10, "    10  ")
    .regex(/^[0-9]+$/, "     "),
  email: z
    .union([z.string().email("  "), z.literal("")])
    .optional(), //  
  addressType: z.enum(["location", "postalCode", "address"], {
    required_error: "    ",
  }),
  location: z.string().optional(), //  ()
  address: z.string().optional(), //   ()
  city: z.string().optional(), //  ()
  postalCode: z
    .string()
    .optional()
    .refine((val) => !val || val === "" || /^[0-9]{10}$/.test(val), {
      message: "   10  ",
    }),
  province: z.string().optional(),
  notes: z
    .string()
    .max(500, "    500  ")
    .optional(),
}).superRefine((data, ctx) => {
  //    location  location   
  if (data.addressType === "location") {
    if (!data.location || data.location.trim().length === 0) {
      ctx.addIssue({
        code: z.ZodIssueCode.custom,
        message: "    ",
        path: ["location"],
      });
    }
  }
  //    postalCode  postalCode   
  if (data.addressType === "postalCode") {
    if (!data.postalCode || data.postalCode.trim().length === 0) {
      ctx.addIssue({
        code: z.ZodIssueCode.custom,
        message: "     ",
        path: ["postalCode"],
      });
    } else if (!/^[0-9]{10}$/.test(data.postalCode)) {
      ctx.addIssue({
        code: z.ZodIssueCode.custom,
        message: "   10  ",
        path: ["postalCode"],
      });
    }
  }
  //    address  address   
  if (data.addressType === "address") {
    if (!data.address || data.address.trim().length < 5) {
      ctx.addIssue({
        code: z.ZodIssueCode.custom,
        message: "   5  ",
        path: ["address"],
      });
    }
  }
});

export type CheckoutFormData = z.infer<typeof checkoutFormSchema>;

/**
 * Sanitize    
 */
export function sanitizeCheckoutInput(data: CheckoutFormData): CheckoutFormData {
  return {
    firstName: data.firstName.trim(),
    lastName: data.lastName?.trim() || "",
    phone: data.phone.replace(/\D/g, ""),
    email: data.email?.trim().toLowerCase() || "",
    addressType: data.addressType,
    location: data.location?.trim() || "",
    address: data.address?.trim() || "",
    city: data.city?.trim() || "",
    postalCode: data.postalCode?.replace(/\D/g, "") || "",
    province: data.province?.trim() || "",
    notes: data.notes?.trim() || "",
  };
}
